.calculator {
  align-items: center;
  display: flex;
  background-color: #000;
  border-radius: 30px;
  height: 700px;
  justify-content: center;
  max-width: 450px;
  padding: 10px;
  width: 100%; }
  @media (max-width: 767px) {
    .calculator {
      height: 100vh; } }
  .calculator .table {
    background: #000;
    height: 100%;
    padding: 7px 5px;
    text-align: center;
    width: 100%; }
    .calculator .table input {
      background-color: transparent;
      border: 0;
      border-bottom: 2px solid #f00;
      font-size: 22px;
      font-weight: bold;
      height: 100px;
      text-align: right;
      padding: 0 25px;
      width: 100%; }
    .calculator .table td {
      width: 25%; }
      .calculator .table td button {
        background-color: #000;
        border: 0;
        border-radius: 15px;
        color: #f00;
        cursor: pointer;
        font-size: 22px;
        font-family: cursive;
        height: 95%;
        width: 95%; }
        .calculator .table td button:hover {
          background-color: #210b04; }
        .calculator .table td button:focus {
          outline: none; }
    .calculator .table .result-area input {
      color: #dbdbdb;
      font-size: 28px; }
      .calculator .table .result-area input:focus {
        outline: none; }
    .calculator .table .clear {
      background: #503026;
      color: #fff; }
      .calculator .table .clear:hover {
        box-shadow: 0 0 0 2px #f00;
        color: #f00; }
    .calculator .table .equal {
      background-color: #210b04; }
      .calculator .table .equal:hover {
        background-color: #503026;
        box-shadow: 0 0 0 2px #f00;
        color: #fff; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  align-items: center;
  background: radial-gradient(#169aa3, #0750bd, #000);
  background-repeat: no-repeat;
  background-attachment: fixed;
  display: flex;
  height: 100vh;
  justify-content: center; }
